
import dateFormat from 'vux/src/tools/date/format.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'

import { mapGetters } from 'vuex'
import NavBar from '@/components/NavBar'
import * as API from '@/api/battle/battle'
export default {
components: {
NavBar,
Group,
Cell,
XInput,
XNumber
},
data() {
return {
operation: 1,
productDetail: {
productId:1,
productName:'白金啊啊啊啊啊啊啊啊啊啊',
bankBuyp:200.8927,
bankSellp:203.2198,
startSell:200.8922,
startBuy:203.8922,
endSell:200.8922,
endBuy:203.8922,
},
accountInfo: {
balance: 10000,
amount: 1000,
},
input: 1,
}
},
created() {
this.operation = this.$route.params.operation
this.productDetail.productId = this.$route.params.productId
this.getProductAccount()
API.getProductDetail(this.productDetail.productId).then(res=>{
this.productDetail = res;
})
},
computed: {
...mapGetters([
'userId'
]),
curTime: function() {
return dateFormat(new Date(), 'HH:mm:ss');
},
mostAmount: function() {
return Math.floor(this.accountInfo.balance / this.productDetail.bankSellp)
},
value: function() {
if(this.operation == 0) { //buy
return (this.input * this.productDetail.bankSellp).toFixed(4)
}else { //sell
return (this.input * this.productDetail.bankBuyp).toFixed(4)
}
}
},
methods: {
submit() {
if(this.operation == 0) {//buy
if(this.input > this.mostAmount) {
this.$vux.alert.show({
title: "失败",
content: "输入数量不能大于可买数量",
onShow() {},
onHide() {}
});
}else {
this.postBuyProduct()
}
}else { //sell
if(this.input > this.accountInfo.amount) {
this.$vux.alert.show({
title: "失败",
content: "输入数量不能大于持仓数量",
onShow() {},
onHide() {}
});
}else {
this.postSellProduct()
}
}
},
getProductAccount() {
API.getProductAccount(this.userId, this.productDetail.productId).then(res=>{
this.accountInfo = res;
})
},
postSellProduct() {
var transactionInfo = {
userId: this.userId,
productId: this.productDetail.productId,
amount: this.input,
lockPrice: this.productDetail.bankBuyp
};
API.postSellProduct(transactionInfo).then(res=>{
if(res.code == 0) { //success
var that = this
this.$vux.alert.show({
title: "成功",
content: "交易成功",
onShow() {},
onHide() {
console.log(that.$router)
that.$router.go(-1);
}
});
}else {
this.$vux.alert.show({
title: "失败",
content: res.data,
onShow() {},
onHide() {}
});
}
})
},
postBuyProduct() {
var transactionInfo = {
userId: this.userId,
productId: this.productDetail.productId,
amount: this.input,
lockPrice: this.productDetail.bankSellp
};
API.postBuyProduct(transactionInfo).then(res=>{
if(res.code == 0) { //success
var that = this
this.$vux.alert.show({
title: "成功",
content: "交易成功",
onShow() {},
onHide() {
console.log(that.$router)
that.$router.go(-1);
}
});
}else {
this.$vux.alert.show({
title: "失败",
content: res.data,
onShow() {},
onHide() {}
});
}
})
}
}
}



