
import { mapGetters } from 'vuex'
import Popup from 'vux/src/components/popup/index.vue'

export default {
name:'popupTop',
props:{
isShow:{
type:Boolean,
default:false
},
autoClose:{
type:Boolean,
default:false
},
showClose:{
type:Boolean,
default:false
}

},
data () {
return {
status:this.isShowPopup
}
},
components:{
Popup,

},
watch:{
isShowPopup (newVal) {
this.status = newVal
},
status (newVal){
this.$store.dispatch('setShowPopup',newVal)
}
},
methods:{
handleHide(){
this.$emit('notifySetting',false)

}
},
computed:{
...mapGetters([
'isShowPopup'
])

}

}




