import NavBar from '@/components/NavBar'
import * as API from '@/api/battle/battle'
export default {
	name: 'account-info',
	data() {
		return {
			userInfo: this.$route.query,
			accountInfo: {
				cardId: 1,
				balance: 323,
				rate: 0.3231,
				accounts: [
					{
						productId: 323,
						productName: '人民币账户白金',
						amount: 323,
					},
					{
						productId: 323,
						productName: '人民币账户白金',
						amount: 323,
					},
					{
						productId: 323,
						productName: '人民币账户白金',
						amount: 323,
					},
					{
						productId: 323,
						productName: '人民币账户白金',
						amount: 323,
					},
					{
						productId: 323,
						productName: '人民币账户白金',
						amount: 323,
					},
				],
			}
		}
	},
	components: {
		NavBar
	},
	methods: {
		getAccounts() {
			console.log('get')
			API.getUserAccount(this.userInfo.userId).then(res => {
				this.accountInfo = res
			})
		},
		toProduct(index) {
			this.$router.push({
				path: "/battle/productdetail",
				query: { obj: this.accountInfo.accounts[index].productId }
			});
		}
	},
	created() {
		this.getAccounts()
	}
}



