
import Vue from 'vue'
import ButtonBar from '@/components/ButtonBar'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

Vue.use(ConfirmPlugin)
export default {
name: 'App',
data () {
return {
isShow:true
}


},
created () {

this.$vux.confirm.prompt('UserId', {
onCancel () {},
onConfirm: (userId) => {
this.$store.dispatch('setUserInfo',userId)
}
})







},
components:{
ButtonBar
},
watch: {
$route (to, from, next) {
if (to.meta.index > from.meta.index) {

this.isShow = false
} else {
this.isShow = true
}
}
}
}



