export default {
  name: "product-list",
  props: {
    products: {
      type: Array
    }
  },
  data() {
    return {};
  },
  methods: {
    toProduct(index) {
      this.$router.push({
        path: "/battle/productdetail",
        query: { obj: this.products[index].obj }
      });
    }
  }
};



