
import store from "@/store/index";
import { mapState, mapActions, mapGetters } from 'vuex'
import getNormalTime from '@/utils/timeFormat'
import 'swiper/dist/css/swiper.css'
import Vue from 'vue'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import popup from '@/components/popup'
import * as API from '@/api/home'
import Group from 'vux/src/components/group/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import BattleSetting from '@/views/BattleHall/BattleSetting'
import PersonalBattle from '@/views/Home/PersonalBattle'
import SearchBox from '@/components/SearchBox'
Vue.use(AlertPlugin)
export default {
name: "home",
created(){
this.$store.dispatch('boardList/getBoardList').then( state => console.log(state));

},
computed : {
...mapGetters([
'userId'
])
},
data() {
return {
swipers:[
process.env.STATIC_URL+'/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg',
process.env.STATIC_URL+'/static/img/6d21c5fa5f66f8f31469320ec1123458.jpeg'
],
showPopup:false,
curUser:0,
showPersonal:false,
ulBoradStyle:{},//排行榜初始高度
swiperOption: {


autoplay: 3000,
grabCursor : true,
setWrapperSize :true,
pagination : '.swiper-pagination',
paginationClickable :true,
prevButton:'.swiper-button-prev',//上一张
nextButton:'.swiper-button-next',//下一张
scrollbar:'.swiper-scrollbar',//滚动条
mousewheelControl : true,
observeParents:true,

debugger: true,
}
}
},
mounted(){
let rem = parseFloat(document.documentElement.style.fontSize)
let board = this.$refs.board.clientHeight / rem - 1; /* h2 的高度为 1rem*/
Vue.set( this.ulBoradStyle, 'height',  Math.floor(board / 1.8) * 1.8* rem  + 'px' )
},
components:{
swiper,
swiperSlide,
PersonalBattle,
BattleSetting,
SearchBox
},
methods:{
expertDetail(){
this.$router.push({path:"/home/expertDetail", query:{}})
},
rule(){
this.$router.push({path:"/home/rule", query:{}})
},
personalBattle(userId){
userId  ? (this.showPersonal = true,this.curUser = userId) : this.showPersonal = false
},
challenge(userId){
if(userId == this.userId) {
this.$vux.alert.show({
title: "提示",
content: "不可以挑战自己哦",
onShow() {},
onHide() {}
});
}
else{
this.curUser = userId
this.$store.dispatch('setShowPopup',true)
}

},
hidePopUp(){
this.$store.dispatch('setShowPopup',false)
},
postNewBattle(){
if(!this.battleDetail.expiredTime){
this.$vux.alert.show({
title: '系统提示',
content: '<br>请设置摆擂时间',
onShow () {
},
onHide () {
}
})
return
}
const battleDetail = {
...this.battleDetail,
founder:this.userId,
duringTime:123,
initialMoney:this.battleDetail.initialMoney*10000,
expiredTime:new Date(this.battleDetail.expiredTime + ":00:00").getTime(),
duringTime:this.battleDetail.duringTime * 86400000 
}
API.postNewBattle(battleDetail).then(()=>{
this.$vux.alert.show({
title: '恭喜',
content: '战书已下达，请等待对方应战！',
onShow () {
},
onHide: ()=> {
this.showPopup = false
}
})
})
}

},
computed: {
expiredTime(){
return  this.getNormalTime+" " + this.minHour;
},
...mapGetters({
boardListUser: 'boardList/allBoardList',
userId:'userId'
})
}
}



