
import store from "@/store/index";
import * as API from "@/api/my";
import BackArrow from "@/components/BackArrow";
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import { mapGetters } from 'vuex'
import popup from "@/components/popup";
import getNormalTime from "@/utils/timeFormat";
import * as DF from "@/utils/timeFormat";
export default {
name: "myMessage",
data() {
return {
tab: 1,
showPopup: false,
messages: [












































































































],
msgDetail: {
index: 0,
senderId: 111,
receiver: 222,
name: "",
portrait: "",
content: "",
messageTime: "",
battleId: "",
initialMoney: "",
duringTime: "",
expiredTime: "",
ifAccepted: 1
}
};
},
components: {
BackArrow,
ButtonTab,
ButtonTabItem,
Divider,
popup,
Alert
},
methods: {
hidePopUp() {
this.$store.dispatch('setShowPopup',false)
},
showDetail(index) {
this.$store.dispatch('setShowPopup',true)
this.msgDetail = this.messages[index];
this.msgDetail.state = this.getState(index);
this.msgDetail.index = index;
},
clickTab(tab) {
this.tab = tab;
if (tab == 1) {
API.getReceivedMsg(this.userId).then(res => {
this.messages = res;
});
} else {
API.getSendMsg(this.userId).then(res => {
this.messages = res;
});
}
},
getState(index) {
if (this.msToDate(this.messages[index].expiredTime.time) < getNormalTime)
return "已失效";
else if (this.messages[index].ifAccepted == 1) return "已接受";
else return "待接受";
},
msToDate(ms) {
return DF.msToDate(ms) ;
},
onAccept() {
console.log("accept challenge" + this.msgDetail.battleId);
var battleDetail = { invitee: this.userId, battleId: this.msgDetail.battleId };
API.postStartBattle(battleDetail).then(res => {
switch (res.userState) {
case 0: //成功
this.msgDetail.ifAccepted = true;
this.messages[this.msgDetail.index].ifAccepted = true;
this.$vux.alert.show({
title: "成功",
content: "您已接受挑战，开始比赛吧",
onShow() {},
onHide: () => {
this.$store.dispatch('setShowPopup',false)
}
});
break;
case 1: //我没空
this.$vux.alert.show({
title: "失败",
content: "您有未完成的比赛，晚点再来吧",
onShow() {},
onHide: () => {
this.$store.dispatch('setShowPopup',false)
}
});
break;
case 2: //对方没空
this.$vux.alert.show({
title: "失败",
content: "对方正在比赛中，晚点再来吧",
onShow() {},
onHide: () => {
this.$store.dispatch('setShowPopup',false)
}
});
break;
}
});
}
},
created() {
API.getReceivedMsg(this.userId).then(res => {
this.messages = res;
});
},
computed:{
...mapGetters([
'userId'
])
}
};



