import * as API from '@/api/my'
import RecordList from '@/components/RecordList'
import NavBar from '@/components/NavBar'
import Vue from 'vue'
import { mapGetters } from 'vuex'
export default {
  name: "myBattles",
  data() {
      return {

          selectTime: "all",
          filterOp: [
              {
                  id:"all",
                  content:"全部",
                  active:true,
              },
              {
                  id:"1",
                  content:"1天内",
                  active:false,
              },
              {
                  id:"3",
                  content:"3天内",
                  active:false,
              },
              {
                  id:"7",
                  content:"7天内",
                  active:false,
              }
          ],
          records: [
              {
                  founderName:"我是长长的用户名hhhhhhhhhhhhhhhhhh",
                  inviteeName:"userB",
                  founderPortrait:"/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
                  inviteePortrait:"/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
                  founderHonor:{url:"/static/icon-img/honor-初出茅庐.png",title:"迷你鸡王"},
                  inviteeHonor:"初出茅庐",
                  founderCardId:"",
                  inviteeCardId:"",
                  startTime:"2018-07-20",
                  endTime:"2018-07-25",
                  initialMoney:"",
                  founderBalance:"",
                  inviteeBalance:"",
                  founderRate:1.1,
                  inviteeRate:1.2,
              },
              {
                  founderName:"我是长长的用户名hhhhhhhhhhhhhhhhhh",
                  inviteeName:"userB",
                  founderPortrait:"/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
                  inviteePortrait:"/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
                  founderHonor:{url:"/static/icon-img/honor-初出茅庐.png",title:"迷你鸡王"},
                  inviteeHonor:"初出茅庐",
                  founderCardId:"",
                  inviteeCardId:"",
                  startTime:"2018-07-20",
                  endTime:"2018-07-29",
                  initialMoney:"",
                  founderBalance:"",
                  inviteeBalance:"",
                  founderRate:1.1,
                  inviteeRate:1.2,
              }
          ]
      }
  },
  created() {

      this.getUserRecords(this.userId, "all");
  },
  methods:{
      tapFilter(id) {
          for(let i=0; i<this.filterOp.length; i++) {
              if(this.filterOp[i].id == id){
                  this.filterOp[i].active = true;
                  this.selectTime = id;
              }
              else
                  this.filterOp[i].active = false;
          }
          this.records = [];
          this.getUserRecords(this.userId, this.selectTime)
      },
      getUserRecords(userId, selectTime) {
        API.getMyBattles(userId, selectTime).then(res=>{
          this.records = res;
          if(this.records.length>0) {
              Vue.set(this.records[0],'collapsed',true);
          }
        })
      }
  },
  components:{
    RecordList,
    NavBar
  },
  computed:{
    ...mapGetters([
      'userId'
    ])
  }
}



