import store from "@/store/index";
import * as API from "@/api/my";
import RecordList from "@/components/RecordList";
import NavBar from "@/components/NavBar";
import Vue from "vue";
import { mapGetters } from 'vuex'
export default {
  name: "myFocus",
  data() {
    return {
      records: [
        {
          founderName: "我是长长的用户名hhhhhhhhhhhhhhhhhh",
          inviteeName: "userB",
          founderPortrait: "/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
          inviteePortrait: "/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
          founderHonor: {
            url: "/static/icon-img/honor-初出茅庐.png",
            title: "迷你鸡王"
          },
          inviteeHonor: "初出茅庐",
          founderCardId: "",
          inviteeCardId: "",
          startTime: "2018-07-20",
          endTime: "2018-07-25",
          initialMoney: "",
          founderBalance: "",
          inviteeBalance: "",
          founderRate: 1.1,
          inviteeRate: 1.2
        },
        {
          founderName: "我是长长的用户名hhhhhhhhhhhhhhhhhh",
          inviteeName: "userB",
          founderPortrait: "/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
          inviteePortrait: "/static/img/5f236c10dc4d2e83d386048aedf9e50c.jpg",
          founderHonor: {
            url: "/static/icon-img/honor-初出茅庐.png",
            title: "迷你鸡王"
          },
          inviteeHonor: "初出茅庐",
          founderCardId: "",
          inviteeCardId: "",
          startTime: "2018-07-20",
          endTime: "2018-07-29",
          initialMoney: "",
          founderBalance: "",
          inviteeBalance: "",
          founderRate: 1.1,
          inviteeRate: 1.2
        }
      ]
    };
  },
  created() {
    this.getMyFocus(this.userId);
  },
  methods: {
    getMyFocus(userId) {
      API.getMyFocus(userId).then(res => {
				this.records = res;
        if (this.records.length > 0) {
          Vue.set(this.records[0], "collapsed", true);
				}
				for(let i = 0; i < this.records.length; i++) {
					Vue.set(this.records[i], "isFocus", 1);
				}
      });
    }
  },
  components: {
    RecordList,
    NavBar
  },
  computed:{
    ...mapGetters([
      'userId'
    ])
  }
};



