
import store from "@/store/index";
import popup from '@/components/popup'
import getNormalTime from '@/utils/timeFormat'
import Vue from 'vue'
import Group from 'vux/src/components/group/index.vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters } from 'vuex'

import * as API from '@/api/home'
Vue.use(AlertPlugin)
export default {
name:'battle-setting',
data () {
return {
battleDetail:{
duringTime:6,
initialMoney:5,
content:null,
expiredTime:'',
currentTime:getNormalTime(),
invitee:'NumberFormatException',
content:''

},
currentTim:getNormalTime,
showPopup:false,
step:0.5,
minHour: new Date().getHours(),
}
},
components:{
popup,
Group,
XButton,
Datetime,
XNumber,
XTextarea
},
computed:{
...mapGetters([
'userId'
])
},
props:{
situation:{
default:'BattleHall',
type:String
},
curUser:{
type:Number,
default:0
}
},
created() {
},
methods:{
postNewBattle(){
if(!this.battleDetail.expiredTime){
this.$vux.alert.show({
title: '系统提示',
content: '<br>请设置摆擂时间',
onShow () {
},
onHide () {
}
})
return
}
let battleDetail = null
if(this.situation !== 'Home'){
battleDetail = {
...this.battleDetail,
founder:this.userId,
content:'NumberFormatException',
initialMoney:this.battleDetail.initialMoney*10000,
expiredTime:new Date(this.battleDetail.expiredTime + ":00:00").getTime(),
duringTime:this.battleDetail.duringTime * 1000 * 60 
}
API.postNewBattle(battleDetail).then(()=>{
this.$vux.alert.show({
title: '恭喜',
content: '擂台已摆好，请等待应战！',
onShow () {
},
onHide: ()=> {
this.$store.dispatch('setShowPopup',false)
}
})
})
}else {
battleDetail = {
...this.battleDetail,
invitee:this.curUser,
founder:this.userId,
initialMoney:this.battleDetail.initialMoney*10000,
expiredTime:new Date(this.battleDetail.expiredTime + ":00:00").getTime(),
duringTime:this.battleDetail.duringTime * 1000 * 60 
}

API.postNewBattle(battleDetail).then(()=>{
this.$vux.alert.show({
title: '恭喜',
content: '战书已下达，请等待应战！',
onShow () {
},
onHide: ()=> {
this.$store.dispatch('setShowPopup',false)
}
})
})
}



}
}

}



